"use client"
import { Container, Row, Col, Form, Button, Card } from 'react-bootstrap';
import 'bootstrap-icons/font/bootstrap-icons.css';
 
function ContactSection(){
    return(
        <Container className="my-5 py-5">
            <Row className="mb-5 text-center">
                <Col>
                    <h2 className="display-5 fw-bold mb-3">Get In Touch</h2>
                    <p className="text-muted fs-5">Have questions? Our watch specialists are here to help you find the perfect timepiece</p>
                </Col>
            </Row>
            <Row className="my-3 py-3">
                <Col md={6} className="mb-4">
                    <Card className="border-0 shadow-sm h-100">
                        <Card.Body className="p-5">
                            <h3 className="fw-bold mb-4">Send Us a Message</h3>
                            <Form className='text-start'>
                                <Form.Group className="mb-3" controlId="formName">
                                    <Form.Label className="fw-semibold">
                                        <i className="bi bi-person me-2"></i>Full Name
                                    </Form.Label>
                                    <Form.Control 
                                        type="text" 
                                        placeholder="Enter your name" 
                                        required 
                                        className="py-2"
                                    />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="formEmail">
                                    <Form.Label className="fw-semibold">
                                        <i className="bi bi-envelope me-2"></i>Email Address
                                    </Form.Label>
                                    <Form.Control 
                                        type="email" 
                                        placeholder="Enter your email" 
                                        required 
                                        className="py-2"
                                    />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="formPhone">
                                    <Form.Label className="fw-semibold">
                                        <i className="bi bi-telephone me-2"></i>Phone Number
                                    </Form.Label>
                                    <Form.Control 
                                        type="tel" 
                                        placeholder="Enter your phone number" 
                                        className="py-2"
                                    />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="formMessage">
                                    <Form.Label className="fw-semibold">
                                        <i className="bi bi-chat-dots me-2"></i>Message
                                    </Form.Label>
                                    <Form.Control 
                                        as="textarea" 
                                        rows={4}
                                        placeholder="Tell us how we can help you..." 
                                        required
                                    />
                                </Form.Group>
                                <Button variant="dark" type="submit" className="w-100 py-2 fw-bold">
                                    <i className="bi bi-send me-2"></i>Send Message
                                </Button>
                            </Form>
                        </Card.Body>
                    </Card>
                </Col>
                <Col md={6} className="mb-4">
                    <Card className="border-0 shadow-sm mb-3">
                        <Card.Body className="p-4">
                            <h4 className="fw-bold mb-4">Visit our Store</h4>
                            <div className="mb-3 d-flex align-items-start">
                                <div className="bg-dark bg-opacity-10 rounded-circle p-3 me-3">
                                    <i className="bi bi-geo-alt-fill fs-5 text-dark"></i>
                                </div>
                                <div>
                                    <h6 className="fw-bold mb-1">Address</h6>
                                    <p className="text-muted mb-0">Jl Mayor Oking Puspanegara<br/>Bogor 16810, Indonesia</p>
                                </div>
                            </div>
                            <div className="mb-3 d-flex align-items-start">
                                <div className="bg-dark bg-opacity-10 rounded-circle p-3 me-3">
                                    <i className="bi bi-telephone-fill fs-5 text-dark"></i>
                                </div>
                                <div>
                                    <h6 className="fw-bold mb-1">Phone</h6>
                                    <p className="text-muted mb-0">+62 821 1085 8345 <br/>+62 821 2263 7844</p>
                                </div>
                            </div>
                            <div className="mb-3 d-flex align-items-start">
                                <div className="bg-dark bg-opacity-10 rounded-circle p-3 me-3">
                                    <i className="bi bi-envelope-fill fs-5 text-dark"></i>
                                </div>
                                <div>
                                    <h6 className="fw-bold mb-1">Email</h6>
                                    <p className="text-muted mb-0">info@timelux.com<br/>support@timelux.com</p>
                                </div>
                            </div>
                            <div className="d-flex align-items-start">
                                <div className="bg-dark bg-opacity-10 rounded-circle p-3 me-3">
                                    <i className="bi bi-clock-fill fs-5 text-dark"></i>
                                </div>
                                <div>
                                    <h6 className="fw-bold mb-1">Business Hours</h6>
                                    <p className="text-muted mb-0">Mon - Sat: 10:00 AM - 8:00 PM<br/>Sunday: 11:00 AM - 6:00 PM</p>
                                </div>
                            </div>
                        </Card.Body>
                    </Card>
                    <Card className="border-0 shadow-sm">
                        <Card.Body className="p-4 text-center">
                            <h5 className="fw-bold mb-3">Connect With Us</h5>
                            <div className="d-flex justify-content-center gap-3">
                                <Button variant="outline-dark" className="rounded-circle" style={{width: "45px", height: "45px"}}>
                                    <i className="bi bi-facebook"></i>
                                </Button>
                                <Button variant="outline-dark" className="rounded-circle" style={{width: "45px", height: "45px"}}>
                                    <i className="bi bi-instagram"></i>
                                </Button>
                                <Button variant="outline-dark" className="rounded-circle" style={{width: "45px", height: "45px"}}>
                                    <i className="bi bi-twitter"></i>
                                </Button>
                                <Button variant="outline-dark" className="rounded-circle" style={{width: "45px", height: "45px"}}>
                                    <i className="bi bi-whatsapp"></i>
                                </Button>
                            </div>
                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </Container>
    )
}
export default ContactSection;