"use client"
import { useState, useEffect } from 'react';
import { Container, Button, Row, Col } from 'react-bootstrap';

const HeroSection = () => {
    const [displayText, setDisplayText] = useState('');
    const roles = ['Full Stack Developer', 'UI/UX Designer', 'Creative Problem Solver'];
    const [roleIndex, setRoleIndex] = useState(0);
    const [charIndex, setCharIndex] = useState(0);
    const [isDeleting, setIsDeleting] = useState(false);

    useEffect(() => {
        const typingSpeed = isDeleting ? 50 : 100;
        const currentRole = roles[roleIndex];

        const timer = setTimeout(() => {
            if (!isDeleting && charIndex < currentRole.length) {
                setDisplayText(currentRole.substring(0, charIndex + 1));
                setCharIndex(charIndex + 1);
            } else if (isDeleting && charIndex > 0) {
                setDisplayText(currentRole.substring(0, charIndex - 1));
                setCharIndex(charIndex - 1);
            } else if (!isDeleting && charIndex === currentRole.length) {
                setTimeout(() => setIsDeleting(true), 1500);
            } else if (isDeleting && charIndex === 0) {
                setIsDeleting(false);
                setRoleIndex((roleIndex + 1) % roles.length);
            }
        }, typingSpeed);

        return () => clearTimeout(timer);
    }, [charIndex, isDeleting, roleIndex]);

    const scrollToSection = (sectionId: string) => {
        const element = document.getElementById(sectionId);
        element?.scrollIntoView({ behavior: 'smooth' });
    };

    return (
        <section 
            id="home" 
            style={{ 
                minHeight: '100vh',
                display: 'flex',
                alignItems: 'center',
                background: '#000000',
                position: 'relative',
                overflow: 'hidden',
                paddingTop: '80px'
            }}
        >
            {/* Animated Background */}
            <div style={{
                position: 'absolute',
                width: '100%',
                height: '100%',
                overflow: 'hidden'
            }}>
                {[...Array(20)].map((_, i) => (
                    <div
                        key={i}
                        style={{
                            position: 'absolute',
                            width: `${Math.random() * 300 + 50}px`,
                            height: `${Math.random() * 300 + 50}px`,
                            borderRadius: '50%',
                            background: 'rgba(255, 255, 255, 0.05)',
                            left: `${Math.random() * 100}%`,
                            top: `${Math.random() * 100}%`,
                            animation: `float ${Math.random() * 10 + 10}s infinite ease-in-out`,
                            animationDelay: `${Math.random() * 5}s`
                        }}
                    />
                ))}
            </div>

            <Container style={{ position: 'relative', zIndex: 1 }}>
                <Row className="align-items-center">
                    <Col lg={7} className="text-white">
                        <div style={{ animation: 'fadeInUp 1s ease-out' }}>
                            <h4 className="mb-3" style={{ 
                                fontSize: '1.2rem',
                                fontWeight: '500',
                                opacity: 0.9
                            }}>
                                üëã Hello, I'm
                            </h4>
                            <h1 
                                className="display-1 fw-bold mb-3"
                                style={{
                                    fontSize: 'clamp(3rem, 8vw, 5rem)',
                                    lineHeight: '1.1',
                                    textShadow: '0 4px 20px rgba(0,0,0,0.2)'
                                }}
                            >
                                Naisya Yuen
                            </h1>
                            <h2 
                                className="display-6 mb-4"
                                style={{
                                    fontSize: 'clamp(1.5rem, 4vw, 2.5rem)',
                                    fontWeight: '400',
                                    minHeight: '80px'
                                }}
                            >
                                <span style={{ 
                                    background: 'rgba(255, 255, 255, 0.2)',
                                    padding: '10px 20px',
                                    borderRadius: '10px',
                                    backdropFilter: 'blur(10px)'
                                }}>
                                    {displayText}
                                    <span style={{ 
                                        animation: 'blink 1s infinite',
                                        marginLeft: '2px'
                                    }}>|</span>
                                </span>
                            </h2>
                            <p className="lead mb-5" style={{ 
                                fontSize: '1.3rem',
                                opacity: 0.95,
                                maxWidth: '600px',
                                lineHeight: '1.8'
                            }}>
                                Crafting beautiful and functional digital experiences with modern technologies. 
                                Passionate about creating solutions that make a difference.
                            </p>
                            <div className="d-flex gap-3 flex-wrap">
                                <Button 
                                    size="lg"
                                    onClick={() => scrollToSection('portfolio')}
                                    className="rounded-pill px-5 py-3 fw-semibold border-0"
                                    style={{
                                        background: 'white',
                                        color: '#000000',
                                        boxShadow: '0 10px 30px rgba(0,0,0,0.2)',
                                        transition: 'all 0.3s ease'
                                    }}
                                    onMouseEnter={(e) => {
                                        e.currentTarget.style.transform = 'translateY(-3px)';
                                        e.currentTarget.style.boxShadow = '0 15px 40px rgba(0,0,0,0.3)';
                                        e.currentTarget.style.background = '#f0f0f0';
                                    }}
                                    onMouseLeave={(e) => {
                                        e.currentTarget.style.transform = 'translateY(0)';
                                        e.currentTarget.style.boxShadow = '0 10px 30px rgba(0,0,0,0.2)';
                                        e.currentTarget.style.background = 'white';
                                    }}
                                >
                                    View My Work
                                </Button>
                                <Button 
                                    size="lg"
                                    onClick={() => scrollToSection('contact')}
                                    variant="outline-light"
                                    className="rounded-pill px-5 py-3 fw-semibold"
                                    style={{
                                        border: '2px solid white',
                                        backdropFilter: 'blur(10px)',
                                        backgroundColor: 'rgba(255, 255, 255, 0.1)',
                                        transition: 'all 0.3s ease'
                                    }}
                                    onMouseEnter={(e) => {
                                        e.currentTarget.style.backgroundColor = 'white';
                                        e.currentTarget.style.color = '#000000';
                                    }}
                                    onMouseLeave={(e) => {
                                        e.currentTarget.style.backgroundColor = 'rgba(255, 255, 255, 0.1)';
                                        e.currentTarget.style.color = 'white';
                                    }}
                                >
                                    Get In Touch
                                </Button>
                            </div>
                            
                            {/* Social Links */}
                            <div className="mt-5 d-flex gap-3">
                                {[
                                    { icon: 'üíº', label: 'LinkedIn', link: '#' },
                                    { icon: 'üêô', label: 'GitHub', link: '#' },
                                    { icon: 'üé®', label: 'Dribbble', link: '#' },
                                    { icon: '‚úâÔ∏è', label: 'Email', link: '#' }
                                ].map((social, index) => (
                                    <a
                                        key={index}
                                        href={social.link}
                                        style={{
                                            width: '50px',
                                            height: '50px',
                                            borderRadius: '50%',
                                            background: 'rgba(255, 255, 255, 0.15)',
                                            backdropFilter: 'blur(10px)',
                                            display: 'flex',
                                            alignItems: 'center',
                                            justifyContent: 'center',
                                            fontSize: '1.5rem',
                                            transition: 'all 0.3s ease',
                                            border: '2px solid rgba(255, 255, 255, 0.3)',
                                            textDecoration: 'none'
                                        }}
                                        onMouseEnter={(e) => {
                                            e.currentTarget.style.background = 'white';
                                            e.currentTarget.style.transform = 'translateY(-5px) scale(1.1)';
                                        }}
                                        onMouseLeave={(e) => {
                                            e.currentTarget.style.background = 'rgba(255, 255, 255, 0.15)';
                                            e.currentTarget.style.transform = 'translateY(0) scale(1)';
                                        }}
                                    >
                                        {social.icon}
                                    </a>
                                ))}
                            </div>
                        </div>
                    </Col>
                    <Col lg={5} className="text-center">
                        <div 
                            style={{
                                animation: 'fadeInRight 1s ease-out',
                                position: 'relative'
                            }}
                        >
                            <div
                                style={{
                                    width: '400px',
                                    height: '400px',
                                    borderRadius: '50%',
                                    background: 'rgba(255, 255, 255, 0.1)',
                                    backdropFilter: 'blur(20px)',
                                    border: '3px solid rgba(255, 255, 255, 0.3)',
                                    display: 'flex',
                                    alignItems: 'center',
                                    justifyContent: 'center',
                                    margin: '0 auto',
                                    fontSize: '15rem',
                                    boxShadow: '0 20px 60px rgba(0,0,0,0.3)',
                                    animation: 'float 6s infinite ease-in-out'
                                }}
                            >
                                üë©‚Äçüíª
                            </div>
                        </div>
                    </Col>
                </Row>
            </Container>

            <style jsx global>{`
                @keyframes fadeInUp {
                    from {
                        opacity: 0;
                        transform: translateY(30px);
                    }
                    to {
                        opacity: 1;
                        transform: translateY(0);
                    }
                }
                @keyframes fadeInRight {
                    from {
                        opacity: 0;
                        transform: translateX(50px);
                    }
                    to {
                        opacity: 1;
                        transform: translateX(0);
                    }
                }
                @keyframes float {
                    0%, 100% {
                        transform: translateY(0px);
                    }
                    50% {
                        transform: translateY(-20px);
                    }
                }
                @keyframes blink {
                    0%, 49% {
                        opacity: 1;
                    }
                    50%, 100% {
                        opacity: 0;
                    }
                }
            `}</style>
        </section>
    );
}

export default HeroSection;