"use client"
import {Container, Row, Col, Card} from 'react-bootstrap';
import 'bootstrap-icons/font/bootstrap-icons.css';

function ServiceSection(){
    const services = [
        {
            icon: "bi-shield-check",
            title: "Authentic Guarantee",
            description: "Every timepiece comes with certificate of authenticity and lifetime warranty",
            color: "primary"
        },
        {
            icon: "bi-truck",
            title: "Free Shipping",
            description: "Complimentary worldwide shipping with secure packaging and insurance",
            color: "success"
        },
        {
            icon: "bi-tools",
            title: "Expert Service",
            description: "Professional maintenance and repair by certified horologists",
            color: "warning"
        },
        {
            icon: "bi-cash-coin",
            title: "Best Price",
            description: "Competitive pricing with flexible payment options and trade-in program",
            color: "info"
        }
    ];

    return(
        <Container className='my-5 py-5'>
            <Row className="mb-5 text-center">
                <Col>
                    <h2 className="display-5 fw-bold mb-3">Why Choose ID Watch</h2>
                    <p className="text-muted fs-5">Experience excellence in every aspect of your watch journey</p>
                </Col>
            </Row>
            <Row>
                {services.map((service, index) => (
                    <Col md={6} lg={3} className="mb-4" key={index}>
                        <Card className="h-100 border-0 shadow-sm hover-lift" style={{transition: "all 0.3s ease"}}>
                            <Card.Body className="text-center p-4">
                                <div className={`bg-${service.color} bg-opacity-10 rounded-circle d-inline-flex align-items-center justify-content-center mb-3`} 
                                     style={{width: "80px", height: "80px"}}>
                                    <i className={`bi ${service.icon} fs-1 text-${service.color}`}></i>
                                </div>
                                <h4 className="fw-bold mb-3">{service.title}</h4>
                                <p className="text-muted">{service.description}</p>
                            </Card.Body>
                        </Card>
                    </Col>
                ))}
            </Row>
            <style jsx>{`
                .hover-lift:hover {
                    transform: translateY(-10px);
                    box-shadow: 0 10px 30px rgba(0,0,0,0.15) !important;
                }
            `}</style>
        </Container>
    )
}

export default ServiceSection;