'use client'
import { useState } from 'react';
import { useRouter } from 'next/navigation';
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import { Container, Row, Col, Badge } from 'react-bootstrap';

interface Project {
    id: number;
    title: string;
    category: string;
    description: string;
    image: string;
    tags: string[];
    color: string;
    link: string;
}

function WorkSection() {
    const [filter, setFilter] = useState('all');
    const [hoveredId, setHoveredId] = useState<number | null>(null);
    const router = useRouter();

    const projects: Project[] = [
        {
            id: 1,
            title: "E-Commerce Platform",
            category: "web",
            description: "Modern online shopping experience with seamless checkout and inventory management system",
            image: "/images/image1.jpeg",
            tags: ["React", "Node.js", "MongoDB"],
            color: "#000000",
            link: "/#contact"
        },
        {
            id: 2,
            title: "Mobile Banking App",
            category: "mobile",
            description: "Secure and intuitive mobile banking solution with real-time transaction tracking",
            image: "/images/image2.jpeg",
            tags: ["React Native", "Firebase", "Redux"],
            color: "#1a1a1a",
            link: "/#contact"
        },
        {
            id: 3,
            title: "Brand Identity Design",
            category: "design",
            description: "Complete brand identity system including logo, colors, typography, and guidelines",
            image: "/images/image3.jpeg",
            tags: ["Figma", "Illustrator", "Branding"],
            color: "#333333",
            link: "/#contact"
        },
        {
            id: 4,
            title: "SaaS Dashboard",
            category: "web",
            description: "Analytics dashboard with real-time data visualization and reporting features",
            image: "/images/image1.jpeg",
            tags: ["Vue.js", "D3.js", "Express"],
            color: "#4d4d4d",
            link: "/#contact"
        },
        {
            id: 5,
            title: "Fitness Tracking App",
            category: "mobile",
            description: "Track workouts, nutrition, and progress with AI-powered recommendations",
            image: "/images/image2.jpeg",
            tags: ["Flutter", "ML Kit", "AWS"],
            color: "#666666",
            link: "/#contact"
        },
        {
            id: 6,
            title: "Portfolio Website",
            category: "design",
            description: "Creative portfolio showcasing work with stunning animations and interactions",
            image: "/images/image3.jpeg",
            tags: ["Next.js", "Framer Motion", "GSAP"],
            color: "#000000",
            link: "/#contact"
        }
    ];

    const filteredProjects = filter === 'all' 
        ? projects 
        : projects.filter(project => project.category === filter);

    const categories = [
        { label: 'All Projects', value: 'all' },
        { label: 'Web Development', value: 'web' },
        { label: 'Mobile Apps', value: 'mobile' },
        { label: 'UI/UX Design', value: 'design' }
    ];

    const handleProjectClick = (link: string) => {
        if (link.startsWith('/#')) {
            const element = document.querySelector(link.substring(1));
            element?.scrollIntoView({ behavior: 'smooth' });
        } else {
            router.push(link);
        }
    };

    return (
        <section id="work" style={{ padding: '100px 0', background: '#ffffff' }}>
            <Container>
                <Row className='mb-5'>
                    <Col md={12} className="text-center">
                        <span 
                            className="badge rounded-pill px-4 py-2 mb-3"
                            style={{
                                background: '#000000',
                                color: 'white',
                                fontSize: '0.9rem'
                            }}
                        >
                            PORTFOLIO
                        </span>
                        <h2 
                            className="display-4 fw-bold mb-3"
                            style={{
                                color: '#000000'
                            }}
                        >
                            Our Latest Work
                        </h2>
                        <p className="lead text-muted" style={{ maxWidth: '700px', margin: '0 auto' }}>
                            Explore our diverse portfolio of successful projects across various industries
                        </p>
                    </Col>
                </Row>

                {/* Filter Buttons */}
                <Row className="mb-5">
                    <Col md={12} className="text-center">
                        <div className="d-inline-flex flex-wrap gap-3 p-3 rounded-pill" 
                            style={{ 
                                background: '#f8f9fa',
                                boxShadow: '0 5px 15px rgba(0,0,0,0.08)'
                            }}
                        >
                            {categories.map((cat) => (
                                <Button
                                    key={cat.value}
                                    onClick={() => setFilter(cat.value)}
                                    className="rounded-pill px-4 py-2 fw-semibold border-0"
                                    style={{
                                        background: filter === cat.value 
                                            ? '#000000'
                                            : 'transparent',
                                        color: filter === cat.value ? 'white' : '#666',
                                        transition: 'all 0.3s ease',
                                        boxShadow: filter === cat.value ? '0 5px 15px rgba(0, 0, 0, 0.3)' : 'none'
                                    }}
                                >
                                    {cat.label}
                                </Button>
                            ))}
                        </div>
                    </Col>
                </Row>

                {/* Projects Grid */}
                <Row>
                    {filteredProjects.map((project) => (
                        <Col md={4} key={project.id} className='mb-4'>
                            <Card 
                                className="h-100 border-0 shadow-sm overflow-hidden"
                                style={{
                                    transform: hoveredId === project.id ? 'translateY(-10px)' : 'translateY(0)',
                                    transition: 'all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275)',
                                    cursor: 'pointer',
                                    boxShadow: hoveredId === project.id 
                                        ? '0 20px 40px rgba(0,0,0,0.15)' 
                                        : '0 5px 15px rgba(0,0,0,0.08)'
                                }}
                                onMouseEnter={() => setHoveredId(project.id)}
                                onMouseLeave={() => setHoveredId(null)}
                            >
                                <div style={{ position: 'relative', overflow: 'hidden', height: '250px' }}>
                                    <Card.Img 
                                        variant="top" 
                                        src={project.image}
                                        style={{
                                            height: '100%',
                                            objectFit: 'cover',
                                            transform: hoveredId === project.id ? 'scale(1.1)' : 'scale(1)',
                                            transition: 'transform 0.6s ease'
                                        }}
                                    />
                                    <div 
                                        style={{
                                            position: 'absolute',
                                            top: 0,
                                            left: 0,
                                            right: 0,
                                            bottom: 0,
                                            background: `linear-gradient(180deg, transparent 0%, ${project.color}dd 100%)`,
                                            opacity: hoveredId === project.id ? 1 : 0,
                                            transition: 'opacity 0.4s ease',
                                            display: 'flex',
                                            alignItems: 'flex-end',
                                            padding: '20px'
                                        }}
                                    >
                                        <div className="d-flex gap-2 flex-wrap">
                                            {project.tags.map((tag, i) => (
                                                <Badge 
                                                    key={i} 
                                                    bg="light" 
                                                    text="dark"
                                                    className="px-3 py-2"
                                                    style={{
                                                        fontSize: '0.75rem',
                                                        fontWeight: '600'
                                                    }}
                                                >
                                                    {tag}
                                                </Badge>
                                            ))}
                                        </div>
                                    </div>
                                </div>
                                <Card.Body className="p-4">
                                    <div className="mb-2">
                                        <Badge 
                                            className="rounded-pill px-3 py-2 mb-3"
                                            style={{
                                                background: `${project.color}20`,
                                                color: project.color,
                                                fontSize: '0.75rem',
                                                fontWeight: '600'
                                            }}
                                        >
                                            {project.category.toUpperCase()}
                                        </Badge>
                                    </div>
                                    <Card.Title className="fw-bold mb-3">{project.title}</Card.Title>
                                    <Card.Text className="text-muted mb-4" style={{ lineHeight: '1.7' }}>
                                        {project.description}
                                    </Card.Text>
                                    <Button 
                                        onClick={() => handleProjectClick(project.link)}
                                        className="rounded-pill px-4 border-0 fw-semibold"
                                        style={{
                                            background: hoveredId === project.id 
                                                ? project.color
                                                : '#f8f9fa',
                                            color: hoveredId === project.id ? 'white' : '#333',
                                            transition: 'all 0.3s ease'
                                        }}
                                    >
                                        View Project ‚Üí
                                    </Button>
                                </Card.Body>
                            </Card>
                        </Col>
                    ))}
                </Row>
            </Container>
        </section>
    );
}

export default WorkSection;